de y no emite juicios? Esa experiencia, que a veces pasa desapercibida, tiene un nombre: empatía. Y es mucho más poderosa de lo que a menudo se imagina. La empatía no solo permite reconocer lo que otro siente, sino que abre la puerta a la transformación emocional, al diálogo y al fortalecimiento de relaciones significativas.")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/10.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("Cuando se explora el papel de la empatía en la transformación de las emociones, se comprende que esta capacidad va más allá de un gesto amable; se convierte en una herramienta fundamental para acompañar, contener y guiar las emociones, tanto en la vida personal como en el entorno educativo. En especial, durante la infancia, etapa en la que las emociones están a flor de piel, la empatía actúa como un canal de contención y de aprendizaje social invaluable.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/11.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("La transformación emocional implica pasar de una emoción que limita, como la rabia o el miedo, a una respuesta que favorece el crecimiento, la comprensión y la conexión. En ese camino, la empatía actúa como una guía interna que permite interpretar la emoción propia y también la del otro. Así, se da lugar a una respuesta más cuidadosa, reflexiva y humana, que no solo alivia tensiones, sino que también educa emocionalmente.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',