 forma de hablar y de actuar. Esta pregunta, que parece sencilla, abre la puerta a una reflexión profunda: ¿cómo se transitan y transforman esas emociones que, a veces, desbordan?")]),_c('div',{staticClass:"bg-color-4 p-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema3/3.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Comprender la transformación de las emociones permite mirar más allá de una reacción momentánea. Se trata de reconocer que las emociones no son inmutables; al contrario, pueden evolucionar si se cuenta con las herramientas necesarias y el acompañamiento adecuado. Este proceso, que comienza por el reconocimiento de lo que se siente, invita a explorar cómo se pueden regular las emociones para que no se conviertan en obstáculos, sino en oportunidades para crecer, vincularse mejor y vivir con mayor bienestar (Pallarés, 2010).")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-7"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/4.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('div',{staticClass:"bg-color-3 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En el contexto de la infancia, esta tarea es aún más importante. Los niños y las niñas experimentan emociones con una intensidad que muchas veces no saben cómo gestionar. Una rabieta, un llanto repentino o una expresión de miedo son respuestas emocionales que deben ser escuchadas, no reprimidas. Aquí se vuelve fundamental el rol del educador: identificar, contener y guiar esas emociones hacia f